<template>
    <div class="cardsBg">
        <v-ons-toolbar class="navBg" fixed-style>
        <div class="left" v-on:click="backToMenu">
            <span class="toolbar-button">
                <ons-icon icon="fa-backward" class="returnBtn"/>
            </span>
        </div>
        <div class="center">
            <h3 class="pageTitle">-{{ cardTitle }}-</h3>
        </div>
        </v-ons-toolbar>
        <transition name="cardsTra" mode="out-in">
            <Learn v-if="view=='Learn'"/>
            <Stock v-if="view=='Stock'"/>
            <Test v-if="view=='Test'"/>
        </transition>

        <div class="tabbar">
        <label class="tabbar__item">
            <input type="radio" name="tabbar-c" value="Learn" v-model="view">
            <button class="tabbar__button">
            <div class="tabbar__label">単語帳</div>
            </button>
        </label>

        <label class="tabbar__item">
            <input type="radio" name="tabbar-c" value="Stock" v-model="view">
            <button class="tabbar__button">
            <div class="tabbar__label">一覧</div>
            </button>
        </label>

        <label class="tabbar__item">
            <input type="radio" name="tabbar-c" value="Test" v-model="view">
            <button class="tabbar__button">
            <div class="tabbar__label">テスト</div>
            </button>
        </label>
        </div>

    </div>
</template>

<style scoped>
    .cardsBg {
        width: 100vw;
        height: 100vh;
        background-image: url(../../public/cardsBg.jpg);
        background-color: rgba(255, 255, 255, 0.4);
        background-blend-mode: lighten;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        position: absolute;
    }
    .returnBtn {
        margin-top: 3px
    }
    .navBg {
        background-color: rgba(255, 255, 255, 0.776);
        height: auto;
    }
    ons-toolbar:not([inline]), ons-bottom-toolbar {
        position: relative;
    }
    .pageTitle {
        font-size: 1.8rem;
        color: rgb(142, 142, 142);
        /* text-shadow: rgb(112, 102, 99) 2px 2px 3px; */
        text-align: center;
        margin: 5px;
    }
    .cards {
        height: 100vh;
        width: 100vw;
    }
    .toolbar {
        margin-bottom: 30px;
    }
    .cardsTra-enter-active, .cardsTra-leave-active {
    transition: all 0.2s;
    }
    .cardsTra-enter {
    transform: translateX(-10px);
    opacity: 0;
    }
    .cardsTra-leave-to {
    transform: translateX(10px);
    opacity: 0;
    }
    .tabbar {
        z-index: 100;
    }

/* mediaquery */
@media screen and (max-width:599px) {
.pageTitle {
    font-size: 26px;
}
}
</style>

<script>
import { mapGetters } from 'vuex'
import store from '../store'
import Learn from '../components/Learn.vue'
import Stock from '../components/Stock.vue'
import Test from '../components/Test.vue'
import firebase from 'firebase/app'

export default {
    components: {
        Learn,
        Stock,
        Test
    },
    data: function() {
        return {
            view: "",
            database: firebase.database(),
            cardPath: this.$route.params.cardsName.slice(1),
            cardTitle: '',
        }
    },
    computed: {
        ...mapGetters([
            'currentView',
            'user'
        ])
    },
    methods: {
        change: function(name) {
            store.commit('currentViewMu', name)
        },
        backToMenu: function() {
            this.$router.push('/Menu')
        }
    },
    created: function() {
        let that = this;
        this.database.ref('user/' + this.user.uid + '/cards/' + this.cardPath).get()
        .then(function(snap){
            that.cardTitle = snap.toJSON().name
        })
    },
    mounted: function() {
        this.view = this.currentView;
    }
}
</script>